import React, { useEffect, useState } from "react";
import Breadcrumb from "../Breadcrumb";
import { Link } from "react-router-dom";
import { Container, Row, Col, Table } from "react-bootstrap";
import { toast, ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import placeholderImg from "../../../assets/images/placeholderImg.svg";
import learnersProfileIco from "../../../assets/images/learnersProfileIco.svg";
import bookLessonTime from "../../../assets/images/clock.svg";
import bookLessonCar from "../../../assets/images/car.svg";
import bookLessonCalendor from "../../../assets/images/calendor.svg";
import bookLessonMap from "../../../assets/images/map.svg";
import phoneIcon from "../../../assets/images/phone.png";
import { useParams } from "react-router-dom";
import { apiGet, apiPost, axiosWithAuth } from "../../../utilities/axiosInstance";
import API_ENDPOINTS from "../../../utilities/apiEndPoints";
import { useAuth } from "../../../context/AuthContext";

export default function LearnerDetails() {
  const { id } = useParams();
  //console.log(id)

  const [learnerDetail, setLearnerDetail] = useState(null);
  const [notes, setNotes] = useState(null);
  const [upcomingBookings, setUpcomingBookings] = useState(null);
  const [bookingHistory, setBookingHistory] = useState(null);
  const [isLoading, setIsLoading] = useState(false);
  const [credit, setCredit] = useState(null);
  const { token } = useAuth();
  const [showPopup, setShowPopup] = useState(false);
  const [addNotes, setAddNotes] = useState();

  useEffect(() => {
    const fetchLearnerDetail = async () => {
      setIsLoading(true);
      try {
        const authInstance = axiosWithAuth(token);
        const responseNew = await authInstance.get(`${API_ENDPOINTS.LEARNER_PROFILE}/${id}`);
        const response = await responseNew.data;
       //console.log(response)
        if (response.success && response.data) {
          setIsLoading(false);
          setLearnerDetail(response.data.learner);
          setCredit(response.data.remaining_credits);
          setUpcomingBookings(response.data.upcoming_bookings);
          setBookingHistory(response.data.booking_history);
          setNotes(response.data.your_notes.note);
        } else {
          console.error("Unexpected response format:", response);
        }
      } catch (error) {
        setIsLoading(false);
        console.error("Error fetching learner detail:", error);
      }
      finally{
        setIsLoading(false)
      }
    };

    fetchLearnerDetail();
  }, [id, token]);


  
const addNewNote = async()=>{
    setIsLoading(true);
    try {
      const authInstance = axiosWithAuth(token);
      const responseNew = await authInstance.post(`${API_ENDPOINTS.ADD_NOTES}`,{
        learner_id:id,
        note:addNotes,
      });
      const response = await responseNew.data;
      //console.log(response)
      if (response.success && response.data) {
        
        toast.success(`${response.message}`);
        setShowPopup(false);
        setNotes(response.data.note);
        setIsLoading(false);
      } else {
        console.error("Unexpected response format:", response);
        toast.error(`${response.message}`);
        setShowPopup(false);
        setIsLoading(false);
      }
    } catch (error) {
      setIsLoading(false);
      console.error("Error fetching learner detail:", error);
    }
  };


  const getStatusClass = (status) => {
    if (status.includes("canceled")) return "text-red";
    if (status.includes("confirmed")) return "text-green";
    return "text-secondary";
  };
  return (
    <div>
      <div className="mainContent">
        <Breadcrumb />
        <div className="mainContent__assets">
          <Container>
            <Row>
              <Col lg={12}>
                {/* learnerList */}
                <div className="learnerProfile">
                  <div className="learnerProfile__left">
                    <h2>Learner Profile</h2>
                    <div className="learnerList__card personalDetailsForm__wrp ">
{learnerDetail?.myself == 1 ? 
                      <div className="learnerList__cardLeft">
                        <div className="learnerList__profileCard">
                          <div className="learnerList__profileImg">
                            <img src={learnerDetail?.profile_picture ||  placeholderImg} alt="" />
                          </div>
                          <div className="learnerList__profileDetails">
                            <div className="learnerList__profileCate">
                              <img src={learnersProfileIco} alt="" />
                              Learner
                            </div>
                            <h3>{learnerDetail?.first_name} {learnerDetail?.last_name}</h3>
                            <a href={`tel:${learnerDetail?.phone_no}`}> <img src={phoneIcon} alt="" /> {learnerDetail?.phone_no}</a>
                            <span>Age {learnerDetail?.age}</span>
                          </div>
                        </div>
                      </div>
:
                      <div className="learnerList__cardLeft">
                        <div className="learnerList__profileCard">
                          <div className="learnerList__profileImg">
                            <img src={learnerDetail?.profile_picture ||  placeholderImg} alt="" />
                          </div>
                          <div className="learnerList__profileDetails">
                            <div className="learnerList__profileCate">
                              <img src={learnersProfileIco} alt="" />
                              Learner
                            </div>
                            <h3>{learnerDetail?.learner_first_name} {learnerDetail?.learner_last_name}</h3>
                            <a href={`tel:${learnerDetail?.learner_phone_no}`}> <img src={phoneIcon} alt="" /> {learnerDetail?.learner_phone_no}</a>
                            <span>Age {learnerDetail?.age}</span>
                          </div>
                        </div>

                        <div className="learnerList__profileCard">
                          <div className="learnerList__profileDetails">
                            <div className="learnerList__profileCate">
                              <img src={learnersProfileIco} alt="" />
                              {learnerDetail?.relation_name}
                            </div>
                            <h3>{learnerDetail?.first_name} {learnerDetail?.last_name}</h3>
                            <a href={`tel:${learnerDetail?.phone_no}`}> <img src={phoneIcon} alt="" /> {learnerDetail?.phone_no}</a>
                            
                          </div>
                        </div>
                      </div>

}
                      <div className="learnerList__cardRight">
                        <div className="learnerList__cardButtons">
                          <button className="btn btn-teal" onClick={()=>{toast.error(`Can't Perform this action`)}}>
                            <svg
                              width="14"
                              height="13"
                              viewBox="0 0 14 13"
                              fill="none"
                              xmlns="http://www.w3.org/2000/svg"
                            >
                              <path
                                d="M1.55556 13C1.12778 13 0.761704 12.8637 0.457333 12.5912C0.152963 12.3187 0.000518518 11.9906 0 11.6071V2.78571C0 2.40268 0.152444 2.07489 0.457333 1.80236C0.762222 1.52982 1.1283 1.39332 1.55556 1.39286H2.33333V0H3.88889V1.39286H10.1111V0H11.6667V1.39286H12.4444C12.8722 1.39286 13.2386 1.52936 13.5434 1.80236C13.8483 2.07536 14.0005 2.40314 14 2.78571V11.6071C14 11.9902 13.8478 12.3182 13.5434 12.5912C13.2391 12.8642 12.8727 13.0005 12.4444 13H1.55556ZM1.55556 11.6071H12.4444V5.57143H1.55556V11.6071ZM1.55556 4.17857H12.4444V2.78571H1.55556V4.17857Z"
                                fill="white"
                              />
                            </svg>
                            PROPOSE BOOKING
                          </button>
                        </div>
                      </div>
                    </div>
                    <div className="personalDetailsForm__wrp">
                      <div className="secIntro mb-0">
                        <div className="secIntro__left">
                          <h4>Remaining Credits</h4>
                        </div>
                        <div className="secIntro__right">
                          <h4>{credit}</h4>
                        </div>
                      </div>
                    </div>
                    <div className="personalDetailsForm__wrp">
                      <div className="secIntro">
                        <div className="secIntro__left">
                          <h4>Your Notes</h4>
                        </div>
                        <div className="secIntro__right">
                          {/* <h4>4 hours left</h4> */}
                        </div>
                      </div>
                      <div className="learnerProfile__writeNote">
                        {notes ? notes :  <>
                        <p>Write your first note about {learnerDetail?.myself == 1 ?   `${learnerDetail?.first_name} ${learnerDetail?.last_name}` : 
                        `${learnerDetail?.learner_first_name} ${learnerDetail?.llearner_last_name}`}
                        <span>(Only you can see these notes)</span> </p>
                        </>
                        }
                        <div className="learnerList__cardRight">
                          <div className="learnerList__cardButtons mt-4">
                            <button className="btn btn-grey" onClick={() => setShowPopup(true)}>
                              <svg
                                width="11"
                                height="10"
                                viewBox="0 0 11 10"
                                fill="none"
                                xmlns="http://www.w3.org/2000/svg"
                              >
                                <path
                                  d="M1 9.5H2.92851L10 2.42851L8.07127 0.5L1 7.57149V9.5Z"
                                  stroke="white"
                                  stroke-linejoin="round"
                                />
                                <path
                                  d="M6.14282 2.4285L8.07133 4.357"
                                  stroke="white"
                                  stroke-linecap="round"
                                  stroke-linejoin="round"
                                />
                              </svg>
                              WRITE A NOTE
                            </button>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="learnerProfile__right">

                    
                    <div className="upcomingBooking">
                      <div className="secIntro ">
                        <div className="secIntro__left">
                          <h4>Upcoming Bookings</h4>
                        </div>
                      </div>

                      {upcomingBookings?.length > 0 ?
                          // pendingBookings.map((booking, index) => (
                            upcomingBookings?.slice(0, 1).map((item, index) =>(
               //   {upcomingBookings?.map((item, i)=>(
                    <div className="bookingCard" key={index}>
                    {/* <div className="bookingCard__top">
                      <h4>Booking {item.bookingNumber} </h4>
                      
                      <div className={`badge ${item.booking_status == 'confirmed' ? 'green' : 'grey'}`}>{item.booking_status == 'confirmed'? 'CONFIRMED':'AWAITING CONFIRMATION'}</div>
                    </div> */}
                    <div className="bookingCard__mid">
                      <div className="bookingCard__profileWrp">

                        <div className="bookingCard__profile">
                          <div className="bookingCard__profileInfo">
                          <div className="bookingCard__highlightedText">
                          <strong>Booking {item.bookingNumber} </strong>
                            </div>
                          </div>
                          {/* <div className="bookingCard__profileImg">
                            <img src={placeholderImg} alt="" />
                          </div>
                          <div className="bookingCard__profileInfo">
                            <div className="bookingCard__highlightedText">
                              <span>LEARNER DETAILS</span>
                            </div>

                            <strong>{item?.learners?.first_name} {item?.learners?.last_name}</strong>
                            <a href={`tel:${item?.learners?.phone_no}`}>{item?.learners?.phone_no}</a>
                          </div> */}
                        </div>

                      </div>
                      <div className="bookingCard__lessonWrp">
                        <div className="bookingCard__highlightedText">
                          <span>LESSON DETAILS</span>
                        </div>
                        <div className="bookingCard__lessonDetails">
                          <img src={bookLessonTime} alt="" />
                           {item?.lesson_details?.lesson_date}
                        </div>
                        <div className="bookingCard__lessonDetails">
                          <img src={bookLessonCar} alt="" />{item?.lesson_details?.duration_transmission}
                        </div>
                        <div className="bookingCard__lessonDetails">
                          <img src={bookLessonCalendor} alt="" />
                           {item?.lesson_details?.lesson_time}
                        </div>
                        <div className="bookingCard__lessonDetails">
                          <img src={bookLessonMap} alt="" />
                          {item?.learners?.address}
                        </div>
                      </div>
                    </div>
                    <div className="bookingCard__bottom">
                      <Link className="btn btn-blue" onClick={()=>{toast.error(`Can't Perform this action`)}}>Manage/View Booking </Link>
                    </div>
                  </div>
                  )) : 'No upcoming bookings'}

                    </div>


                    <div className="accordians">
                      <div className="secIntro mb-0">
                        <div className="secIntro__left">
                          <h4>Booking History</h4>
                        </div>
                      </div>
                      <div className="accordians__card">
                        <div className="accordians__description">
                          <Table>
                            <thead>
                              <tr>
                                <th>Booking</th>
                                <th colSpan={3}>Details</th>
                              </tr>
                            </thead>
                            <tbody>
                        {bookingHistory?.length > 0 ? 
                          // pendingBookings.map((booking, index) => (
                            bookingHistory?.slice(0, 4).map((booking, index) =>(
                         // {historyBookings.map((booking, index) => (
                            <tr key={index}>
                              <td>{booking.bookingNumber}</td>
                              <td colSpan={2}>
                              {booking?.lesson_details?.lesson_date}, {booking?.lesson_details?.lesson_time}
                              <p>{booking?.learners?.address}</p>
                              </td>
                             
                              <td
                                className={`font-weight-bold ${getStatusClass(
                                  booking?.booking_status
                                )}`}
                              >
                                <div className="d-flex align-items-center">
                                  <div className="statusIco"></div>
                                  {booking.booking_status}
                                </div>
                              </td>
                            </tr>
                          )) : <tr><td colSpan={2}><div style={{textAlign:'center'}}>No booking history</div></td></tr>}
                        </tbody>
                          </Table>
                          {/* Pagination */}
                          <div className="pagination-container text-center d-none">
                            <button className="btn btn-light">&lt;</button>
                            <span className="mx-3">1</span>
                            <button className="btn btn-light">&gt;</button>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </Col>
            </Row>
          </Container>
        </div>
      </div>
{/* popup to add note */}

 {/* Popup */}
 {showPopup && (
                            <div className="popup">
                              <Container>
                                <Row>
                                  <Col lg={12}>
                                    <div className="popup__wrp personalDetailsForm__wrp">
                                      <div className="personalDetailsForm__top text-center position-relative">
                                        <h4>Add a Note</h4>
                                        <button
                                          type="button"
                                          className="popup__close"
                                          onClick={() => setShowPopup(false)}
                                        >
                                          X
                                        </button>
                                      </div>
                                      <div className="personalDetailsForm__bottom">
                                        <div className="RegistrationForm__field">
                                         
                                          <textarea className="form-control" placeholder="Write your note here"
                                            value={addNotes}
                                            onChange={(e) => setAddNotes(e.target.value)}
                                          ></textarea>
                                        </div>

                                        <div className="d-flex justify-content-center ">
                                          <button
                                            className="btn btn-teal"
                                            onClick={ addNewNote}
                                            disabled={isLoading}
                                          >
                                             {isLoading ? 'Adding note...' : 'Add'}
                                          </button>
                                        </div>
                                      </div>
                                    </div>
                                  </Col>
                                </Row>
                              </Container>
                            </div>
                          )}

{/* popup to add note */}
<ToastContainer />

    </div>
  );
}
